import { stateKey } from './reducer';
export const selectOnboardingState = (state) => state[stateKey];
export const selectOnboardingName = (state) => selectOnboardingState(state).name;
export const selectOnboardingConfirm = (state) => selectOnboardingState(state).isConfirmShown;
export const selectOnboardingType = (state) => selectOnboardingState(state).type;
export const selectOnboardingClusterID = (state) => selectOnboardingState(state).clusterId;
export const selectOnboardingS3BucketName = (state) => selectOnboardingState(state).s3BucketName;
export const selectOnboardingArn = (state) => selectOnboardingState(state).arn;
export const selectOnboardingSourceKindChecked = (state) => selectOnboardingState(state).sourceKindChecks;
export const selectOnboardingValidation = (state) => ({
    nameValid: selectOnboardingState(state).nameValid,
    typeValid: selectOnboardingState(state).typeValid,
    clusterIdValid: selectOnboardingState(state).clusterIdValid,
    s3BucketNameValid: selectOnboardingState(state).s3BucketNameValid,
    arnValid: selectOnboardingState(state).arnValid,
});
export const selectOnboardingDirty = (state) => {
    const obState = selectOnboardingState(state);
    return {
        name: obState.nameDirty,
        type: obState.typeDirty,
        clusterId: obState.clusterIdDirty,
        s3BucketName: obState.s3BucketNameDirty,
        arn: obState.arnDirty,
    };
};
export const selectOnboardingIsInvalid = (state) => {
    const data = selectOnboardingValidation(state);
    return Object.keys(data).some(k => data[k] === false);
};
export const selectOnboardingModal = (state) => selectOnboardingState(state).isOpen;
export const selectApiErrors = (state) => selectOnboardingState(state).apiErrors;
export const selectApiStatus = (state) => selectOnboardingState(state).apiStatus;
